@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
html{font-family:"Montserrat", sans-serif}*{margin:0;padding:0}body{background-color:#008080}

.title{text-align:center;margin:30px}.Carousel{display:flex;margin:0 auto;justify-content:center;box-shadow:0 5px 25px black;width:-webkit-min-content;width:min-content;border-radius:5px}.Carousel__list{position:relative;order:2;display:flex;list-style:none;overflow:hidden;-webkit-transform:translateX(0);transform:translateX(0);border-top:1px solid #fdfffc;border-bottom:1px solid #fdfffc;background-color:#fdfffc}.Carousel__slide{display:flex;flex-shrink:0;transition-timing-function:linear;-webkit-transform:translateX(0);transform:translateX(0);background-color:transparent}.Carousel__slide img{will-change:transform;width:100%;height:100%}.Carousel .button{border:1px solid #fdfffc;z-index:2;padding:10px;font-size:1.5rem;color:#fdfffc;font-weight:bold;cursor:pointer;background-color:#e36414}.Carousel .button:disabled{background-color:#853b0c;border:1px solid #853b0c}.Carousel .buttonPrev{box-shadow:0 5px 15px #525252;border-top-left-radius:5px;border-bottom-left-radius:5px;border-right:none;order:1}.Carousel .buttonNext{box-shadow:0 -5px 15px #525252;border-top-right-radius:5px;border-bottom-right-radius:5px;border-left:none;order:3}.form{margin:40px auto;display:flex;align-items:center;flex-direction:column;grid-gap:5px;gap:5px}label{min-width:20%;max-width:30%;font-size:1rem;font-weight:500;display:flex;padding-left:10px;align-items:baseline;grid-gap:10px;gap:10px}.input{outline:none;border:none;width:80px;font-weight:bold;padding:5px 0  5px 10px;margin-left:auto;border-radius:3px}.checkbox{align-self:baseline;-webkit-transform:scale(1.3);transform:scale(1.3);margin-left:auto;margin-right:75px}

